.container {
    width: 100%;
    background-color: rgba(0, 0, 0,.85);
    color: #E9A6A6;
    padding: 15px;
    box-shadow: .25rem .25rem rgba(0,0,0,.55);
    overflow: hidden;
}

.container hr {
    border: .1rem dashed #E9A6A6;
    margin: .2rem 0;
}

.container:hover {
    cursor: pointer;
}

.top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.arrow img {
    height: 1rem;
}

.top:hover .arrow, 
.arrow:hover, 
.clearContainer:hover, 
.filterContainer:hover {
    animation: var(--arrow-animation);
}


.open {
    display: block;
}

.closed {
    display: none;
}

.options {
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}


.optionsContainer {
    padding-top: 5px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.clearContainer {
    width: 43%;
    display: flex;
    justify-content: left;
    align-items: center;
}

.clearContainer p {
    font-size: .85rem;
    font-weight: bolder;
    color: red;
    margin-left: .85rem;    
}

.clearContainer img {
    height: 1rem;
}

.filterContainer {
    width: 43%;
    display: flex;
    justify-content: right;
    align-items: center;
}

.filterContainer p {
    font-size: .85rem;
    font-weight: bolder;
    margin-right: .85rem;    
}

.filterContainer img {
    height: 1rem;
}

@media screen and (max-width: 1000px) {
    .options {
        width: 100%;
        padding: 5px 0;
        display: block;
    }
}

@media screen and (max-width: 500px) {
    .filterContainer p {
        font-size: .75rem;
    }
    .clearContainer p {
        font-size: .75rem;
    }
    .filterContainer img {
        height: .85rem;
    }
    .clearContainer img {
        height: .85rem;
    }
}

@media screen and (max-width: 380px) {
    .filterContainer p {
        font-size: .55rem;
    }
    .clearContainer p {
        font-size: .65rem;
    }
    .filterContainer img {
        height: .55rem;
    }
    .clearContainer img {
        height: .65rem;
    }
}